import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { AngularFireStorage } from '@angular/fire/storage';
import { User } from './user';
import { Farm } from './farm';
import { Income } from './income';
import { Outcome } from './outcome';
import { Production } from './production';
import { FarmStats } from './farm-stats';
import { ProductionStats } from './production-stats';
import { IncomeStats } from './income-stats';
import { OutcomeStats } from './outcome-stats';

@Injectable()
export class ContentService {

    usersList: AngularFireList<any>;
    farmsList: AngularFireList<any>;
    farmStatsList: AngularFireList<any>;
    incomesList: AngularFireList<any>;
    incomeStatsList: AngularFireList<any>;
    outcomesList: AngularFireList<any>;
    outcomeStatsList: AngularFireList<any>;
    productionList: AngularFireList<any>;
    productionStatsList: AngularFireList<any>;
    incomeStats: IncomeStats = new IncomeStats();
    statsListIn: IncomeStats[];
    tempStatsListIn: IncomeStats[];
    outcomeStats: OutcomeStats = new OutcomeStats();
    statsListOut: OutcomeStats[];
    tempStatsListOut: OutcomeStats[];
    productionStats: ProductionStats = new ProductionStats();
    statsListPr: ProductionStats[];
    tempStatsListPr: ProductionStats[];
    farmStats: FarmStats = new FarmStats();
    statsListFa: FarmStats[];
    tempStatsListFa: FarmStats[];

    prList: Production[];
    faList: Farm[];
    inList: Income[];
    outList: Outcome;

    regionsList: AngularFireList<any>;

    constructor(
        private afDb: AngularFireDatabase,
        private afS: AngularFireStorage
    ) {
        this.usersList = this.afDb.list('users');
        this.farmsList = this.afDb.list('farms');
        this.incomesList = this.afDb.list('incomes');
        this.incomeStatsList = this.afDb.list('income-stats');
        this.outcomesList = this.afDb.list('outcomes');
        this.outcomeStatsList = this.afDb.list('outcome-stats');
        this.productionList = this.afDb.list('production');
        this.productionStatsList = this.afDb.list('production-stats');
        this.farmStatsList = this.afDb.list('farm-stats');
        this.regionsList = this.afDb.list('regions');
    }

    refreshProductionStats() {

        /* this.productionList.snapshotChanges().subscribe(item => {
            this.prList = [];
            item.forEach(element => {
                const y = element.payload.toJSON();
                y['pid'] = element.key;
                this.productionList.push(y as Production);
            });
        }); */
    }

    refreshFarmStats() {

        this.farmStatsList.remove();
        this.farmsList.snapshotChanges().subscribe(item => {
            const x = 0;
            let tempS: FarmStats[];
            item.forEach(element => {
                const y = element.payload.toJSON();
                this.editFarmStatistics((y as Farm), tempS);
            });
        });
    }

    refreshIncomeStats() {

    }

    refreshOutcomeStats() {

    }

    getfarmStats() {
        this.farmStatsList = this.afDb.list('farm-stats');
        return this.farmStatsList;
    }

    getRegions() {
        this.regionsList = this.afDb.list('regions');
        return this.regionsList;
    }

    getUsersData() {
        this.usersList = this.afDb.list('users');
        return this.usersList;
    }

    updateUser(user: User) {
        this.usersList.update(user.uid, {
            provider: user.provider,
            firstName: user.firstName,
            lastName: user.lastName,
            email: user.email,
            photo: user.photo,
            role: user.role,
            about: user.about,
            city: user.city,
            region: user.region
        });
    }

    getFarmsData() {
        this.farmsList = this.afDb.list('farms');
        return this.farmsList;
    }

    insertFarm(farm: Farm) {
        this.farmsList.push({
            farmerid: farm.farmerid,
            title: farm.title,
            region: farm.region,
            longitude: farm.longitude,
            latitude: farm.latitude,
            size: farm.size,
            soil: farm.soil,
            description: farm.description,
            registrationDate: new Date().getTime()
        });
        this.editFarmStatistics(farm, this.statsListFa);
    }

    getFarmStatistics() {
        this.farmStatsList = this.afDb.list('farm-stats');
        this.farmStatsList.snapshotChanges().subscribe(item => {
            this.statsListFa = [];
            item.forEach(element => {
                const y = element.payload.toJSON();
                y['fsid'] = element.key;
                if ((y as FarmStats).region === 'Heraklion Crete') {
                    const farmSt = new FarmStats();
                    farmSt.fsid = (y as FarmStats).fsid;
                    farmSt.region = (y as FarmStats).region;
                    if (this.statsListFa[0]) {
                        farmSt.size = this.statsListFa[0].size + (y as FarmStats).size;
                    } else {
                        farmSt.size = (y as FarmStats).size;
                    }
                    this.statsListFa.splice(0, 0, farmSt);
                } else if ((y as FarmStats).region === 'Chania Crete') {
                    const farmSt = new FarmStats();
                    farmSt.fsid = (y as FarmStats).fsid;
                    farmSt.region = (y as FarmStats).region;
                    if (this.statsListFa[0]) {
                        farmSt.size = this.statsListFa[0].size + (y as FarmStats).size;
                    } else {
                        farmSt.size = (y as FarmStats).size;
                    }
                    this.statsListFa.splice(1, 0, farmSt);
                } else if ((y as FarmStats).region === 'Rethimnon Crete') {
                    const farmSt = new FarmStats();
                    farmSt.fsid = (y as FarmStats).fsid;
                    farmSt.region = (y as FarmStats).region;
                    if (this.statsListFa[0]) {
                        farmSt.size = this.statsListFa[0].size + (y as FarmStats).size;
                    } else {
                        farmSt.size = (y as FarmStats).size;
                    }
                    this.statsListFa.splice(2, 0, farmSt);
                } else if ((y as FarmStats).region === 'Lasithi Crete') {
                    const farmSt = new FarmStats();
                    farmSt.fsid = (y as FarmStats).fsid;
                    farmSt.region = (y as FarmStats).region;
                    if (this.statsListFa[0]) {
                        farmSt.size = this.statsListFa[0].size + (y as FarmStats).size;
                    } else {
                        farmSt.size = (y as FarmStats).size;
                    }
                    this.statsListFa.splice(3, 0, farmSt);
                }
            });
        });
    }

    editFarmStatistics(farm: Farm, statsListFa: FarmStats[]) {
        if (statsListFa) {
            if (statsListFa.length !== 0) {
                if (farm.region === 'Heraklion Crete') {
                    if (statsListFa[0] !== null) {
                        this.farmStats.fsid = statsListFa[0].fsid;
                        this.farmStats.size = statsListFa[0].size + farm.size;
                        this.farmStats.region = farm.region;
                        this.updateFarmStats(this.farmStats);
                    } else {
                        this.farmStats.size = farm.size;
                        this.farmStats.region = farm.region;
                        this.insertFarmStats(this.farmStats);
                    }
                } else if (farm.region === 'Chania Crete') {
                    if (this.statsListFa[1]) {
                        this.farmStats.fsid = statsListFa[1].fsid;
                        this.farmStats.size = statsListFa[1].size + farm.size;
                        this.farmStats.region = farm.region;
                        this.updateFarmStats(this.farmStats);
                    } else {
                        this.farmStats.size = farm.size;
                        this.farmStats.region = farm.region;
                        this.insertFarmStats(this.farmStats);
                    }
                } else if (farm.region === 'Rethimnon Crete') {
                    if (this.statsListFa[2]) {
                        this.farmStats.fsid = statsListFa[2].fsid;
                        this.farmStats.size = statsListFa[2].size + farm.size;
                        this.farmStats.region = farm.region;
                        this.updateFarmStats(this.farmStats);
                    } else {
                        this.farmStats.size = farm.size;
                        this.farmStats.region = farm.region;
                        this.insertFarmStats(this.farmStats);
                    }
                } else if (farm.region === 'Lasithi Crete') {
                    if (this.statsListFa[3]) {
                        this.farmStats.fsid = statsListFa[3].fsid;
                        this.farmStats.size = statsListFa[3].size + farm.size;
                        this.farmStats.region = farm.region;
                        this.updateFarmStats(this.farmStats);
                    } else {
                        this.farmStats.size = farm.size;
                        this.farmStats.region = farm.region;
                        this.insertFarmStats(this.farmStats);
                    }
                }
            }
        } else {
            this.farmStats.size = farm.size;
            this.farmStats.region = farm.region;
            this.insertFarmStats(this.farmStats);
        }
    }

    updateFarmStats(farmStats: FarmStats) {
        this.farmStatsList.update(farmStats.fsid, {
            size: farmStats.size,
            region: farmStats.region,
            registrationDate: new Date().getTime()
        });
    }

    insertFarmStats(farmStats: FarmStats) {
        this.farmStatsList.push({
            size: farmStats.size,
            region: farmStats.region,
            registrationDate: new Date().getTime()
        });
    }

    updateFarm(farm: Farm) {
        this.farmsList.update(farm.fid, {
            farmerid: farm.farmerid,
            title: farm.title,
            region: farm.region,
            longitude: farm.longitude,
            latitude: farm.latitude,
            size: farm.size,
            soil: farm.soil,
            description: farm.description,
            registrationDate: farm.registrationDate
        });
    }

    deleteFarm(fid: string) {
        this.farmsList.remove(fid);
    }

    getIncomesData() {
        this.incomesList = this.afDb.list('incomes');
        return this.incomesList;
    }

    insertIncome(income: Income) {
        this.incomesList.push({
            farmerid: income.farmerid,
            title: income.title,
            ammount: income.ammount,
            description: income.description,
            registrationDate: new Date().getTime()
        });
        this.editIncomeStatistics(income);
    }

    getIncomeStatistics() {
        this.incomeStatsList = this.afDb.list('income-stats');
        this.incomeStatsList.snapshotChanges().subscribe(item => {
            this.statsListIn = [];
            item.forEach(element => {
                const y = element.payload.toJSON();
                y['isid'] = element.key;
                this.statsListIn.push(y as IncomeStats);
            });
        });
    }

    editIncomeStatistics(income: Income) {
        const date = new Date();
        const monthYear = String((date.getMonth()) + 1) + '/' + String(date.getFullYear());
        const ammount = income.ammount;
        if (this.statsListIn.length !== 0) {
            this.tempStatsListIn = this.statsListIn.filter(
                stats => stats.date === monthYear);
            if (this.tempStatsListIn.length !== 0) {
                this.incomeStats = this.tempStatsListIn[0];
                this.incomeStats.ammount = this.incomeStats.ammount + ammount;
                console.log('Updated income stats.');
                this.updateIncomeStats(this.incomeStats);
            } else {
                this.incomeStats.ammount = ammount;
                this.incomeStats.date = monthYear;
                console.log('Added new income stats.');
                this.insertIncomeStats(this.incomeStats);
            }
        } else {
            this.incomeStats.ammount = ammount;
            this.incomeStats.date = monthYear;
            console.log('Added new income stats.');
            this.insertIncomeStats(this.incomeStats);
        }
    }

    updateIncomeStats(incomeStats: IncomeStats) {
        this.incomeStatsList.update(incomeStats.isid, {
            ammount: incomeStats.ammount,
            date: incomeStats.date,
            registrationDate: new Date().getTime()
        });
    }

    insertIncomeStats(incomeStats: IncomeStats) {
        this.incomeStatsList.push({
            ammount: incomeStats.ammount,
            date: incomeStats.date,
            registrationDate: new Date().getTime()
        });
    }

    updateIncome(income: Income) {
        this.incomesList.update(income.iid, {
            farmerid: income.farmerid,
            title: income.title,
            ammount: income.ammount,
            description: income.description,
            registrationDate: income.registrationDate
        });
    }

    deleteIncome(iid: string) {
        this.incomesList.remove(iid);
    }

    getOutcomesData() {
        this.outcomesList = this.afDb.list('outcomes');
        return this.outcomesList;
    }

    insertOutcome(outcome: Outcome) {
        this.outcomesList.push({
            farmerid: outcome.farmerid,
            title: outcome.title,
            ammount: outcome.ammount,
            description: outcome.description,
            registrationDate: new Date().getTime()
        });
        this.editOutcomeStatistics(outcome);
    }

    getOutcomeStatistics() {
        this.outcomeStatsList = this.afDb.list('outcome-stats');
        this.outcomeStatsList.snapshotChanges().subscribe(item => {
            this.statsListOut = [];
            item.forEach(element => {
                const y = element.payload.toJSON();
                y['osid'] = element.key;
                this.statsListOut.push(y as OutcomeStats);
            });
        });
    }

    editOutcomeStatistics(outcome: Outcome) {
        const date = new Date();
        const monthYear = String((date.getMonth()) + 1) + '/' + String(date.getFullYear());
        const ammount = outcome.ammount;
        if (this.statsListOut.length !== 0) {
            this.tempStatsListOut = this.statsListOut.filter(
                stats => stats.date === monthYear);
            if (this.tempStatsListOut.length !== 0) {
                this.outcomeStats = this.tempStatsListOut[0];
                this.outcomeStats.ammount = this.outcomeStats.ammount + ammount;
                console.log('Updated outcome stats.');
                this.updateOutcomeStats(this.outcomeStats);
            } else {
                this.outcomeStats.ammount = ammount;
                this.outcomeStats.date = monthYear;
                console.log('Added new outcome stats.');
                this.insertOutcomeStats(this.outcomeStats);
            }
        } else {
            this.outcomeStats.ammount = ammount;
            this.outcomeStats.date = monthYear;
            console.log('Added new outcome stats.');
            this.insertOutcomeStats(this.outcomeStats);
        }
    }

    updateOutcomeStats(outcomeStats: OutcomeStats) {
        this.outcomeStatsList.update(outcomeStats.osid, {
            ammount: outcomeStats.ammount,
            date: outcomeStats.date,
            registrationDate: new Date().getTime()
        });
    }

    insertOutcomeStats(outcomeStats: OutcomeStats) {
        this.outcomeStatsList.push({
            ammount: outcomeStats.ammount,
            date: outcomeStats.date,
            registrationDate: new Date().getTime()
        });
    }

    updateOutcome(outcome: Outcome) {
        this.outcomesList.update(outcome.oid, {
            farmerid: outcome.farmerid,
            title: outcome.title,
            ammount: outcome.ammount,
            description: outcome.description,
            registrationDate: outcome.registrationDate
        });
    }

    deleteOutcome(oid: string) {
        this.outcomesList.remove(oid);
    }

    getProductionData() {
        this.productionList = this.afDb.list('production');
        return this.productionList;
    }

    insertProduction(production: Production) {
        this.productionList.push({
            farmerid: production.farmerid,
            title: production.title,
            ammountWeight: production.ammountWeight,
            ammount: production.ammount,
            description: production.description,
            registrationDate: new Date().getTime()
        });
        this.editProductionStatistics(production);
    }

    getProductionStatistics() {
        this.productionStatsList = this.afDb.list('production-stats');
        this.productionStatsList.snapshotChanges().subscribe(item => {
            this.statsListPr = [];
            item.forEach(element => {
                const y = element.payload.toJSON();
                y['psid'] = element.key;
                this.statsListPr.push(y as ProductionStats);
            });
        });
    }

    editProductionStatistics(production: Production) {
        const date = new Date();
        const monthYear = String((date.getMonth()) + 1) + '/' + String(date.getFullYear());
        const ammountWeight = production.ammountWeight;
        if (this.statsListPr.length !== 0) {
            this.tempStatsListPr = this.statsListPr.filter(
                stats => stats.date === monthYear);
            if (this.tempStatsListPr.length !== 0) {
                this.productionStats = this.tempStatsListPr[0];
                this.productionStats.ammount = this.productionStats.ammount + ammountWeight;
                console.log('Updated production stats.');
                this.updateProductionStats(this.productionStats);
            } else {
                this.productionStats.ammount = ammountWeight;
                this.productionStats.date = monthYear;
                console.log('Added new production stats.');
                this.insertProductionStats(this.productionStats);
            }
        } else {
            this.productionStats.ammount = ammountWeight;
            this.productionStats.date = monthYear;
            console.log('Added new production stats.');
            this.insertProductionStats(this.productionStats);
        }
    }

    updateProductionStats(productionStats: ProductionStats) {
        this.productionStatsList.update(productionStats.psid, {
            ammount: productionStats.ammount,
            date: productionStats.date,
            registrationDate: new Date().getTime()
        });
    }

    insertProductionStats(productionStats: ProductionStats) {
        this.productionStatsList.push({
            ammount: productionStats.ammount,
            date: productionStats.date,
            registrationDate: new Date().getTime()
        });
    }

    updateProduction(production: Production) {
        this.productionList.update(production.pid, {
            farmerid: production.farmerid,
            title: production.title,
            ammountWeight: production.ammountWeight,
            ammount: production.ammount,
            description: production.description,
            registrationDate: production.registrationDate
        });
    }

    deleteProduction(pid: string) {
        this.productionList.remove(pid);
    }
}
