import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ContentService } from '../core/content.service';
import { Production } from '../core/production';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-total-production',
  templateUrl: './total-production.component.html',
  styleUrls: ['./total-production.component.css']
})
export class TotalProductionComponent implements OnInit {

  public tableData: TableData;

  productionList: Production[];
  constructor(
    private router: Router,
    private contentService: ContentService
  ) { }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Farmer ID', 'Title', 'Ammount Weight', 'Ammount', 'Description']
    };
    const x = this.contentService.getProductionData();
    x.snapshotChanges().subscribe(item => {
      this.productionList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['pid'] = element.key;
        this.productionList.push(y as Production);
      });
    });
  }

}
